@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.navbar {
  background-image: url(/static/media/HomeHeroPic.93ad38ce.jpg);
  background-size: cover; /* Adjust the background size as needed */
  background-repeat: no-repeat;
  background-position: right top -6px;
  background-attachment: scroll;
  height: 80px; /* Reduce the height to make it more compact */
  display: flex;
  justify-content: space-between; /* Separate the logo and menu items */
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky; /* Set the navbar position to sticky */
  top: 0; /* Stick it to the top of the viewport */
  z-index: 100; /* Ensure it's on top of other elements */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a shadow for a nice effect */
  padding: 0 20px; /* Add some padding on the sides */
}

.nav-menu {
  display: flex;
  align-items: center;
  grid-gap: 30px;
  gap: 30px; /* Increase the gap between menu items */
  list-style: none;
  margin: 0;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: rgb(41, 41, 98);
  text-decoration: none;
  padding: 0.5rem 1rem;
  position: relative;
  float: left;
  top: 20%;
  left: 30%; /* Add a relative position for the underline */
}

.nav-links::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px; /* Position the underline slightly below the link */
  width: 100%;
  height: 2px;
  background-color: darkblue;
  -webkit-transform: scaleX(0);
          transform: scaleX(0); /* Start with no underline */
  -webkit-transform-origin: left;
          transform-origin: left; /* Anchor point of the underline to the left */
  transition: -webkit-transform 0.9s ease;
  transition: transform 0.9s ease;
  transition: transform 0.9s ease, -webkit-transform 0.9s ease; /* Smooth transition with 0.9s duration */
}

.nav-links:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1); /* Fully underline on hover */
}

.fa-bars {
  color: rgb(41, 41, 98);
  font-size: 1.5rem;
  cursor: pointer; /* Add cursor pointer on the menu icon */
}

.navbar-top-icons {
  display: flex;
  align-items: center; /* Vertically align the icons */
  grid-gap: 10px;
  gap: 10px; /* Adjust the gap between the icons */
}

.navbar-top-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #3b5998;
  color: white;
  font-size: 20px;
  text-decoration: none;
  transition: background-color 0.2s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.navbar-top-icon i {
  line-height: 1;
}

.navbar-top-icon:hover {
  background-color: #4267b2; /* Change the background color on hover */
}

/* Add a subtle scale effect on hover */
.navbar-top-icon:hover i {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
img {
  position: absolute;
  top: -11px; /* Move the image up by 10 pixels */
  left: 4%;
  width: 220px;
  height: 110px;
  z-index: 10; /* Set a higher z-index to make it cover anything above */
}



@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    border: none;
  }

  .nav-menu.active {
    background: white;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: black;
  }

  .nav-links:hover {
    background-color: lightblue;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: black;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #1888ff;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }

  button {
    display: none;
  }
  .navbar-top-icons {
    display: none;
  }
}

